.msg {
  width: 75vw;
  /* width: 100%; */
  background-color: rgb(35, 35, 35);
  /* background-image: url(../../Items/yourname1.jpg); */
  background-repeat: no-repeat;
  background-size: 100vw;
  color: white;
  height: 100dvh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  transition: width 0.3s ease-in-out;
  /* border: 1px solid white; */
}

::-webkit-scrollbar {
  scrollbar-width: none;
}

.personalchat {
  height: 81vh;
  width: 70vw;
  padding: 20px 12px;
  flex: 1;
  overflow-x: hidden; 
  position: relative; 

  .date-header{
    margin: 2vh 20vw 2vh 20vw;
    border-radius: 10px;
    border:1px solid  #ffffff;
    text-align: center;
    background-color:#7f7f7f96;
    padding: 3px 0 5px 0;
    cursor: default;
  }

  .message {
    max-width: 70%;

    &.own {

      .text {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 5px;
        width: 70vw;
        margin-bottom: 20px;
        align-items: end;
        position: relative;

        img {
          display: block;
          width: 60vmin;
          height: auto;
          border-radius: 10px;
          object-fit: cover;
        }
        p {
          padding: 20px;
          background-color: #5183fe;
          border-radius: 20px 0 20px 20px;
          align-self: flex-end;
          max-width: 88vmin;
          word-wrap: break-word;
          overflow-wrap: break-word;
          position: relative; 


          &:after {
            content: "";
            position: absolute;
            top: 0;
            right: -10px;
            width: 10px;
            height: 10px;
            background: linear-gradient(
              135deg,
              #5183fe 0%,
              #5183fe 50%,
              transparent 50%,
              transparent
            );
          }
        }

        span {
          font-size: 10px;
          align-self: flex-end;
          cursor: default;
        }
      }
    }

    &.frnd {
      .text {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-bottom: 20px;

        img {
          width: 60vmin;
          height: auto;
          border-radius: 10px;
          object-fit: cover;
        }

        p {
          padding: 20px;
          border-radius: 0 20px 20px 20px;
          background-color: #33487d70;
          /* background-color: #6789da66; */
          backdrop-filter: blur(3px);
          align-self: flex-start;
          max-width: 88vmin;
          word-wrap: break-word;
          overflow-wrap: break-word;
          position: relative;

          

          &:after {
            content: "";
            position: absolute;
            top: 0;
            left: -10px;
            width: 10px;
            height: 10px;
            background: linear-gradient(
              225deg,
              #33487d70 0%,
              #33487d70 50%,
              transparent 50%,
              transparent
            );
            backdrop-filter: blur(3px);
          }
        }
      }

      span {
        font-size: 10px;
        cursor: default;
      }
    }
  }
}

.tdetails {
  width: 70%;
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin-bottom: 30px;
}

.send {
  button {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: none;
    font-size: 30px;
    color: #5183fe;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.tinp {
  min-height: 50px;
  width: 100%;
  display: flex;
  align-items: flex-end;
  background-color: rgb(21, 21, 21);
  margin-right: 10px;
  border-radius: 15px 15px 0 15px;
  padding-right: 15px;
  position: relative;

  .picker {
    position: absolute;
    bottom: 50px;
  }
  p {
    padding-bottom: 15px;
    color: rgb(135, 135, 135);
    font-weight: 50;
  }
}

.micclip {
  display: flex;
  gap: 3px;
  /* background-color: red; */
  border-radius: 15px;
  cursor: pointer;
  z-index: 0;

  .micclip-icn{
    padding: 15px 20px 15px 20px;
    font-size: 20px;
    border-radius: 15px;

    &:hover{
      background-color: rgb(68, 68, 68);
    }
  }

}
.inpMsg {
  flex: 1;
  border: none;
  max-height: 150px;
  background-color: transparent;
  color: white;
  outline: none;
  padding: 15px 15px 0px 20px;
  resize: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  overflow-y: auto;
}

.inpMsg::placeholder {
  color: rgb(135, 135, 135);
}

.ticon {

  padding: 15px 20px 15px 20px;
  font-size: 20px;
  cursor: pointer;

  &:hover{
    background-color: rgb(68, 68, 68);
    border-radius: 15px;
  }
}

.box{
  position: absolute;
  bottom: 55px;
  right: 0;
  background-color: rgb(21, 21, 21);
  border-radius: 15px;
  padding: 10px;
  /* opacity: 0; */
  /* display: flex; */
  justify-content: center;
  flex-direction: column;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
  cursor: default;
  /* z-index: -1; */
  display: none;
}

.attachFiles{
  display: flex;
  /* opacity: 1; */
  transform: translateY(0);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.attach-Icn{
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;

  .icn-div{
    width: 20px;
  }

  &:hover{
    background-color: rgb(68, 68, 68);
    /* cursor: pointer; */
  }
}

.webcam{
  position: absolute;
  top: 9vh;
  
}




.spinner1 {
  position: absolute;
  top: calc(50% - 4.5vmin);
  left: calc(50% - 4.5vmin);
  transform: translate(-50%, -50%);
  width: 7vmin;
  height: 7vmin;
  border-radius: 50%;
  border: 9px solid #3780ef;
  animation: spinner-bulqg1 0.8s infinite linear alternate,
       spinner-oaa3wk 1.6s infinite linear;
 
}

@keyframes spinner-bulqg1 {
  0% {
     clip-path: polygon(50% 50%, 0 0, 50% 0%, 50% 0%, 50% 0%, 50% 0%, 50% 0%);
  }

  12.5% {
     clip-path: polygon(50% 50%, 0 0, 50% 0%, 100% 0%, 100% 0%, 100% 0%, 100% 0%);
  }

  25% {
     clip-path: polygon(50% 50%, 0 0, 50% 0%, 100% 0%, 100% 100%, 100% 100%, 100% 100%);
  }

  50% {
     clip-path: polygon(50% 50%, 0 0, 50% 0%, 100% 0%, 100% 100%, 50% 100%, 0% 100%);
  }

  62.5% {
     clip-path: polygon(50% 50%, 100% 0, 100% 0%, 100% 0%, 100% 100%, 50% 100%, 0% 100%);
  }

  75% {
     clip-path: polygon(50% 50%, 100% 100%, 100% 100%, 100% 100%, 100% 100%, 50% 100%, 0% 100%);
  }

  100% {
     clip-path: polygon(50% 50%, 50% 100%, 50% 100%, 50% 100%, 50% 100%, 50% 100%, 0% 100%);
  }
}

@keyframes spinner-oaa3wk {
  0% {
     transform: scaleY(1) rotate(0deg);
  }

  49.99% {
     transform: scaleY(1) rotate(135deg);
  }

  50% {
     transform: scaleY(-1) rotate(0deg);
  }

  100% {
     transform: scaleY(-1) rotate(-135deg);
  }
}


.preview{
  position: fixed;
  background-color:rgba(0, 0, 0, 0.682);
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100dvh;
  width: 100vw;
  z-index: 10000;
  flex-direction: column;
  gap: 1vh;
}

.preview img{
  height: 70%;
  width: auto;
}

.preview button{
  height: 5vmin;
  width: 17vmin;
  border-radius: 10px;
  border: none;
  color: #ffffff;
  font-weight: bold;
  font-size: larger;
  background-color: #5183fe;
}



@media (width < 481px) {
  .msg {
    width: 100vw;
    /* background-image: url(../../Items/yourname.jpg); */
    background-repeat: no-repeat; 
    background-position: top 8vh center;
  }
  .tdetails {
    width: 98vw;
    margin-bottom: 2vh;
  }
  .tinp {
    width: 82vw;
    margin-right: 1vw;
    padding-right: 0;
  }
  .ticon {
    padding: 15px 10px 15px 13px;
    font-size: 20px;
  }
  .inpMsg {
    padding: 15px 5px 0px 5px;
  }
  .micclip {
    display: flex;
    .micclip-icn{
      padding: 15px 10px 15px 10px;
    }
  }
  .webcam{
    position: absolute;
    top:0;
    bottom: 0;
    height: 100dvh;
    width: 100vw;
    
  }
  .personalchat{
    width: 93vw;
    padding: 20px 4vw;
    .message {

      &.own {
  
        .text {
          width: 93vw;
          p{
            padding: 3vmin;
            border-radius:  10px 0 10px 10px;
            max-width: 60vw;
            &:after {
              right: -7px;
              width: 7px;
              height: 7px;
            }
          }
        }
      }
      &.frnd {
        .text {
          p{
            padding: 3vmin;
            max-width: 60vw;
            border-radius: 0 10px 10px 10px;
            &:after {
              left: -7px;
              width: 7px;
              height: 7px;
            }
          }
        }
      }
    }
  }
}
